.security_group {
  padding: 0.26rem 0.3rem;
}
.security_group .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.security_group .com-add-btn {
  margin-bottom: 0.12rem;
}
.security_group table {
  border: 1px solid #D9DBE1;
}
.security_group .ant-table-tbody > tr > td:first-child,
.security_group .ant-table-thead > tr > th:first-child {
  padding-left: 0.6rem;
}
.security_group .link {
  cursor: pointer;
}
.security_group .link:hover {
  text-decoration: underline;
}
.security_group .ant-pagination {
  padding-left: 0;
  padding-right: 0;
}
/* group_rules */
.group_rules .ant-tabs-nav {
  padding: 0 0.3rem;
}
.group_rules .ant-tabs-nav::before {
  border-color: #D9DBE1;
}
.group_rules .top {
  display: flex;
  justify-content: space-between;
}
.group_rules .ant-tabs-tab {
  line-height: 1;
  padding: 0.27rem 0 0.2rem 0;
  font-size: 0.16rem;
  color: #9696A3;
  margin-right: 0.4rem;
}
.group_rules .opt-box {
  display: flex;
  padding: 0.25rem 0.3rem 0.3rem;
}
.group_rules .opt-box .btn {
  line-height: 0.4rem;
  padding: 0 0.16rem;
  border-radius: 0.04rem;
  cursor: pointer;
  font-size: 0.14rem;
  margin-right: 0.1rem;
}
.group_rules .opt-box .add {
  background: #5E5CE3;
  color: #fff;
}
.group_rules .opt-box .batchAdd {
  background: #F1F1FE;
  color: #5E5CE3;
}
.group_rules .opt-box .batchDelete {
  background: #F2F1F6;
  color: #525666;
}
.group_rules .multiple tr td:first-child,
.group_rules .multiple tr th:first-child {
  padding-left: 0.3rem;
}
.group_rules .multiple tr td:first-child div,
.group_rules .multiple tr th:first-child div,
.group_rules .multiple tr td:first-child label,
.group_rules .multiple tr th:first-child label {
  position: relative;
  z-index: 10;
}
.group_rules .multiple tr td:nth-child(2),
.group_rules .multiple tr th:nth-child(2) {
  padding-left: 0.56rem;
}
.group_rules .cloud-tab tr td:first-child,
.group_rules .cloud-tab tr th:first-child {
  padding-left: 0.6rem;
}
.group_rules .relation {
  padding: 0 0.3rem 0.3rem 0.3rem;
}
.group_rules .relation .top {
  margin-bottom: 0.12rem;
}
.group_rules .cloud-tab {
  border: 1px solid #D9DBE1;
}
/* cloud-modal */
.cloud-modal .empty-tip.ant-form-item,
.cloud-modal .ant-form-item-label-left {
  font-size: 0.14rem;
  color: #525666;
  line-height: 1;
  height: auto;
}
.cloud-modal .empty-tip {
  margin-bottom: 0.15rem;
}
.cloud-modal .empty-tip label {
  line-height: 1;
  height: auto;
}
.cloud-modal .ant-form-item-control-input {
  min-height: auto;
}
/* batch-modal */
.batch-modal {
  width: 6.6rem !important;
}
.batch-modal .t-tit {
  margin-bottom: 0.15rem;
}
.batch-modal .t-tit span {
  display: inline-block;
  margin-right: 4px;
  color: #ff4d4f;
  font-size: 14px;
  font-family: SimSun, sans-serif;
  line-height: 1;
}
.batch-modal p {
  margin-bottom: 0;
}
.batch-modal .ant-checkbox-wrapper {
  margin-right: 0.3rem;
}
.batch-modal .ant-checkbox-wrapper + .ant-checkbox-wrapper {
  margin-left: 0;
}
.batch-modal #batch .empty-tip {
  margin-bottom: 0;
}
.batch-modal #batch .empty-tip:nth-child(4) {
  margin-bottom: 0.15rem;
}
